body {
  margin: 0;
  font-family: Arial, sans-serif;
}

nav {
  background-color: white;
  /* color: crimson; */
  overflow: hidden;
  box-shadow: 5px 5px 20px #555;
}

nav a {
  float: left;
  display: block;
  color: crimson;
  font-size: larger;
  font-weight: bolder;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.logo {
  float: left;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  padding: 14px 16px;

}
.logoo{
  height: 40px;
}
.options {
  float: right;
}

@media screen and (max-width: 600px) {
  nav a, .logo, .options {
    float: none;
    display: block;
    text-align: center;
    width: 100%;
  }
}

/* header */
.fubrl {
  margin-top: 10px;
}
.mynav{
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 3px 3px 20px #555;
}

.headermain {
  padding-top: 10px;
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: white;
  align-items: center;
  color: crimson;
  font-weight: bold;
  font-size: large;
  border-bottom: 1px solid gray;
  box-shadow: 3px 3px 5px gray;
}

.div1h {
  width: 50%;
  display: flex;
  cursor: pointer;
}

.div1h p {
  width: 15%;
  text-align: right;
  padding: 5px;
}

.div1h img {
  padding-left: 30px;
}

.logo {
  height: 50px;
  width: 30%;
}

.div2h {
  width: 50%;
}

.div2h ul {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  cursor: pointer;
}

.li:hover {
  background-color: gray;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

/* Optionally, you can add transition for a smooth effect */
.li {
  transition: all ease-in-out 0.2s;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
}

.close {
  color: white;
  float: right;
  font-size: 18px;
  font-weight: bold;
  border-radius: 5px;
  height: 50px;
  width: 50px;
  background-color: red;
  border: none;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.mymodal {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 100%;
  margin-bottom: 5px;
}

/* footer */

.footer {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: large;
  font-weight: bold;
  color: #585353;
}

.span {
  font-size: xx-large;
}

.link {
  text-decoration: none;
  color: #585353;
}

