/* Main content styles */
.homemain {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 20px;
}

.fari {
  font-size: 2rem;
  font-weight: 900;
  color: crimson;
  margin: 0;
}

.lines {
  font-weight: 400;
  color: rgb(44, 42, 42);
}

.btn {
  background-color: crimson;
  height: 50px;
  width: 200px;
  font-weight: 500;
  color: white;
  box-shadow: 3px 3px 10px #555;
  margin-top: 20px;
}

/* Banner styles */
.banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.bannerimg {
  border-radius: 10px;
  box-shadow: 5px 5px 20px #555555ac;
  max-width: 100%;
  height: auto;
}

/* Gallery styles */
.hrr {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  font-weight: bold;
  margin-top: 50px;
}

.hr {
  width: 100px;
  border: 2px solid black;
  background-color: black;
  margin-bottom: 20px;
}

.gallery {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
}

.img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

/* Map styles */
.map {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

