/* Address styles */
.address {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.adp {
  font-size: 20px;
  font-weight: bold;
  text-align: left;
  margin-bottom: 10px;
}

.call {
  cursor: pointer;
}

/* Adjustments for smaller screens */
@media screen and (max-width: 768px) {
  .address {
    flex-direction: column;
    align-items: center;
  }
}

